html {
  box-sizing     : border-box;
  width          : 100vw;
  overflow-x     : hidden;
  scroll-behavior: smooth;
  user-select    : none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  color                  : #212121;
  background-color       : #fff;
  -webkit-font-smoothing : antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display  : block;
  max-width: 100%;
  height   : auto;
}

button {
  outline: none;
}

/*
 * Стили компонента App
 */
.App {
  display              : grid;
  grid-template-columns: 1fr;
  grid-gap             : 16px;
  padding-bottom       : 24px;
}



/*
 * Стили компонента Searchbar
 */
.Searchbar {
  top             : 0;
  left            : 0;
  position        : sticky;
  z-index         : 1100;
  display         : flex;
  justify-content : center;
  align-items     : center;
  min-height      : 64px;
  padding-right   : 24px;
  padding-left    : 24px;
  padding-top     : 12px;
  padding-bottom  : 12px;
  color           : #fff;
  background-color: #3f51b5;
  box-shadow      : 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

/*
 * Стили компонента SearchForm
 */
.SearchForm {
  display         : flex;
  align-items     : center;
  width           : 100%;
  max-width       : 600px;
  background-color: #fff;
  border-radius   : 3px;
  overflow        : hidden;
}


.SearchForm-button {
  display            : inline-block;
  width              : 48px;
  height             : 48px;
  border             : 0;
  background-image   : url('https://image.flaticon.com/icons/svg/149/149852.svg');
  background-size    : 40%;
  background-repeat  : no-repeat;
  background-position: center;
  opacity            : 0.6;
  transition         : opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor             : pointer;
  outline            : none;
}

.SearchForm-button:hover {
  opacity: 1;
}

.SearchForm-button-label {
  position   : absolute;
  width      : 1px;
  height     : 1px;
  padding    : 0;
  overflow   : hidden;
  clip       : rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path  : inset(50%);
  border     : 0;
}

.SearchForm-input {
  display      : inline-block;
  width        : 100%;
  font         : inherit;
  font-size    : 20px;
  border       : none;
  outline      : none;
  padding-left : 4px;
  padding-right: 4px;
}

.SearchForm-input::placeholder {
  font     : inherit;
  font-size: 18px;
}

/*
 * Стили компонента ImageGallery
 */

.ImageGallery {
  display              : grid;
  max-width            : 1340px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap             : 16px;
  margin-top           : 10px;
  margin-bottom        : 0;
  padding              : 0;
  list-style           : none;
  margin-left          : auto;
  margin-right         : auto;
}

/*
 * Стили компонента ImageGalleryItem
 */
.Container {
  padding         : 0 0 10px 0;
  transition      : 1s;
  transition-delay: 1s;
}

.Loader {
  padding-top        : 50px;
  text-align         : center;
  /* background-color: black; */
}

.ErrorMessage {
  display   : block;
  text-align: center;
  color     : #3f51b5;
}

.ImageGalleryItem {
  transition-delay: 1s;
  border-radius   : 2px;
  box-shadow      : 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.ImageGalleryItem-image {
  transition      : 1s;
  transition-delay: 1s;
  width           : 100%;
  height          : 260px;
  object-fit      : cover;
  transition      : transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem-image:hover {
  transition      : 1s;
  transition-delay: 1s;
  transform       : scale(1.03);
  cursor          : zoom-in;
}

/*
 * Стили компонента Modal
 */

.Overlay {
  position        : fixed;
  top             : 0;
  left            : 0;
  width           : 100vw;
  height          : 100vh;
  display         : flex;
  justify-content : center;
  align-items     : center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index         : 1200;
}

.Modal {
  max-width : calc(100vw - 48px);
  max-height: calc(100vh - 24px);
}

/*
* Стили компонента Buton (Load more)
*/
.Button {
  display         : block;
  margin          : auto;
  margin-top      : 10px;
  box-sizing      : border-box;
  padding         : 8px 16px;
  border-radius   : 2px;
  background-color: #3f51b5;
  transition      : all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align      : center;
  color           : #fff;
  border          : 0;
  text-decoration : none;
  cursor          : pointer;
  font-family     : inherit;
  font-size       : 18px;
  line-height     : 24px;
  font-style      : normal;
  font-weight     : 500;
  min-width       : 180px;
  box-shadow      : 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.Button:hover,
.Button:focus {
  background-color: #303f9f;
}

.modal {
    position: relative;
  display: flex;
}

.close_modal_btn {
position: absolute;
top: 0;
right: 0;
  width: 40px;
  height: 40px;
  font-weight: bold;
  margin-left: 10px;
  background-color: #3f51b5;
  color: #fff;
}


.close_modal_btn:hover {
  background-color:#fff;
  color: #3f51b5;
}

.ImgGallery-overlay{
  position: relative;
  display: flex;
}